/** Scaffolding **/
#header {
  grid-row: 1;
  grid-column: 1 / 3;

  display: grid;
  grid-template-columns: 1fr auto;
  border-bottom: 5px solid;
  /** Alexa light ring effect */
  -webkit-border-image: -webkit-gradient(
      linear,
      left top, right top,
      from(#5291de),
      color-stop(40%, #5291de),
      color-stop(42%, #16eaed),
      color-stop(#16eaed),
      color-stop(58%, #16eaed),
      color-stop(60%, #5291de),
      to(#5291de)
    )
    60;
  -webkit-border-image: -webkit-linear-gradient(
      left,
      #5291de,
      #5291de 40%,
      #16eaed 42%,
      #16eaed,
      #16eaed 58%,
      #5291de 60%,
      #5291de
    )
    60;
       -o-border-image: -o-linear-gradient(
      left,
      #5291de,
      #5291de 40%,
      #16eaed 42%,
      #16eaed,
      #16eaed 58%,
      #5291de 60%,
      #5291de
    )
    60;
          border-image: -webkit-gradient(
      linear,
      left top, right top,
      from(#5291de),
      color-stop(40%, #5291de),
      color-stop(42%, #16eaed),
      color-stop(#16eaed),
      color-stop(58%, #16eaed),
      color-stop(60%, #5291de),
      to(#5291de)
    )
    60;
          border-image: linear-gradient(
      to right,
      #5291de,
      #5291de 40%,
      #16eaed 42%,
      #16eaed,
      #16eaed 58%,
      #5291de 60%,
      #5291de
    )
    60;
}

#header-info {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.app-bar {
  background: #152939 !important;
}



