/** Scaffolding **/
#leftpanel {
  display: grid;
  grid-template-rows: auto 30px;
  height: calc(100vh - 90px);
}

.panel-body {
  overflow: auto;
}

/** Styles **/
#leftpanel {
  border-right: 1px solid #d3c5cf;
}

#leftpanel #chat-input {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}



